<template>
  <div style="width: 100%; height: 100vh; background-color: lightskyblue;overflow: hidden">
      <div style="width: 400px; margin: 200px auto">
        <div style="color: black; font-size: 30px; text-align: center; padding: 30px">Welcome to Register</div>
        <el-form ref="form" :model="form" label-width="80px" :rules="rules">
          <el-form-item label="Username:" prop="username">
            <el-input v-model="form.username" prefix-icon="el-icon-user-solid"></el-input>
          </el-form-item>
          <el-form-item label="Password:" prop="password">
            <el-input v-model="form.password" prefix-icon="el-icon-lock" show-password></el-input>
          </el-form-item>
          <el-form-item label="Confirm:" prop="confirm">
            <el-input v-model="form.confirm" prefix-icon="el-icon-lock" show-password></el-input>
          </el-form-item>
          <el-form-item >
            <el-button style="width: 100%;" type="success" @click="register">Register</el-button>
          </el-form-item>
        </el-form>
      </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "Register",
  data() {
    return {
      form:{},
      rules: {
        username: [
          {required: true, message: 'Please enter username', trigger: 'blur'},
          // { min: 6, max: 10, message: 'Username needs to be between 6 and 10 characters long', trigger: 'blur' }
        ],

        password: [
          {required: true, message: 'Please enter password', trigger: 'blur'},
          // { min: 6, max: 10, message: 'Username needs to be between 6 and 10 characters long', trigger: 'blur' }
        ],
        confirm: [
          {required: true, message: 'Please enter confirm password', trigger: 'blur'},
          // { min: 6, max: 10, message: 'Username needs to be between 6 and 10 characters long', trigger: 'blur' }
        ],
      }
      // backgroundDiv: {
      //
      //   backgroundImage:'url(' + require('../assets/background.jpg') + ')',
      //
      //   backgroundRepeat:'no-repeat',
      //
      //   backgroundSize:'100% 100%',
      //
      //
      // }
    }
  },

  methods:{


    register(){

      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (this.form.password === this.form.confirm) {
            request.post('/user/register', this.form).then(res => {
              if (res.code === '0') {
                this.$message({
                  type: "success",
                  message: "Register successfully"
                })
                this.$router.push("/login")  //get in next page
              } else {
                this.$message({
                  type: "error",
                  message: res.msg
                })
              }
            })
          }else {
            this.$message({
              type:"error",
              message:"Oops! The passwords don't match"
            })
          }
        }
      })
    }
  }
}
</script>

